export default {
	props: {
		productDataSource: {
			type: String,
			value: ''
		}
	},
	data() {
		return {
			productList: [],
			loadingText: '加载中...',
			currentPage: 1,
			lastPage: 1,
			pageSize: 10
		};
	},
	onReady() {
		this.loadProducts()
	},
	methods: {
		//商品跳转
		toProduct(productId) {
			uni.showToast({
				title: '商品' + productId,
				icon: 'none'
			});
			uni.navigateTo({
				url: '../product/product-detail?productId=' + productId,
			});
		},
		loadProducts() {
			const dataSource = this.productDataSource
			if (dataSource == null) {
				dataSource = '/commons/product/new/'
			}
			let that = this
			this.$pin.request('GET', dataSource + this.currentPage + '/' + this.pageSize, null,
				successData => {
					console.log(successData)
					that.productList = that.productList.concat(successData.data.list)
					that.lastPage = successData.data.lastPage
					that.currentPage = successData.data.pageNum
					if (this.currentPage >= this.lastPage) {
						this.loadingText = '-到底了-';
						return false;
					}
				},
				failData => {
					console.log(failData)
					uni.showToast({
						icon: 'none',
						title: '加载商品出错'
					})
				}
			)
		},
		loadNextPage() {
			let len = this.productList.length;
			if (this.currentPage >= this.lastPage) {
				this.loadingText = '-到底了-';
				return false;
			}
			this.currentPage++;
			this.loadProducts()
		},
	}
}
